import React from 'react';
import echarts from 'echarts';

import "../styles/BhvCnt.css"

export default class UsrValue extends React.Component {
    componentDidMount() {
        let option = {
            title: {
                text: '用户价值划分',
                left: 'center',
                top: 20,
                textStyle: {
                    color: '#333'
                }
            },
        
            tooltip: {
                trigger: 'item',
                formatter: '{a} <br/>{b} : {c} ({d}%)'
            },

            legend: {
                show: true,
                right: 0,
                top: "40%",
                orient: "vertical",
            },
        
            visualMap: {
                show: false,
                min: 80,
                max: 600,
                inRange: {
                    colorLightness: [0.4, 0.6]
                }
            },
            series: [
                {
                    name: '用户类别',
                    type: 'pie',
                    radius: '55%',
                    center: ['50%', '50%'],
                    data: [
                        { "name": "价值用户", "value": 237653 },
                        { "name": "发展用户", "value": 220582 },
                        { "name": "保持用户", "value": 48965 },
                        { "name": "挽留用户", "value": 165204 },
                        { "name": "未消费用户", "value": 311701 }
                    ].sort(function (a, b) { return a.value - b.value; }),
                    roseType: false,
                    label: {
                        color: 'rgba(0, 0, 0, 0.7)',
                        show: true,
                        fontSize: 16,
                    },
                    labelLine: {
                        lineStyle: {
                            color: 'rgba(0, 0, 0, 0.7)'
                        },
                        smooth: 0.2,
                        length: 20,
                        length2: 60
                    },
                    itemStyle: {
                        shadowBlur: 100,
                        shadowColor: 'rgba(0, 0, 0, 0.4)'
                    },
        
                    animationType: 'scale',
                    animationEasing: 'elasticOut',
                    animationDelay: function (idx) {
                        return Math.random() * 200;
                    }
                }
            ]
        };
        let echart = echarts.init(document.getElementById("tar"));
        echart.setOption(option);
    }
    render() {
        return (
            <div className="single-container">
                <div className="single-block" id="tar"></div>
            </div>
        );
    }
}