import React from 'react';
import ReactDOM from 'react-dom';
import './styles/index.css';
import Sider from './components/Sider';
import UserDay from './components/UserDay';
import {
    BrowserRouter as Router,
    Switch,
    Route
} from "react-router-dom";
import UserHour from './components/UserHour';
import UserHourCompare from './components/UserHourCompare';
import BhvCnt from './components/BhvCnt';
import UsrCnt from './components/UsrCnt';
import UsrValue from './components/UsrValue';

class App extends React.Component {
    render() {
        return (
            <Router>
                <div className="container">
                    <nav>
                        <Sider />
                    </nav>
                    <main>
                        <Switch>
                            <Route path="/user-day">
                                <UserDay />
                            </Route>
                            <Route path="/user-hour-compare">
                                <UserHourCompare />
                            </Route>
                            <Route path="/user-hour">
                                <UserHour />
                            </Route>
                            <Route path="/bhv-cnt">
                                <BhvCnt />
                            </Route>
                            <Route path="/usr-cnt">
                                <UsrCnt />
                            </Route>
                            <Route path="/usr-value">
                                <UsrValue />
                            </Route>
                        </Switch>
                    </main>
                </div>
            </Router>
        );
    }
}

ReactDOM.render(<App />, document.getElementById("root"));