import React from 'react';
import { Link, useLocation } from "react-router-dom";
import "../styles/Sider.css"
import { Menu } from 'antd';
import { LineChartOutlined, PieChartOutlined, PayCircleOutlined } from '@ant-design/icons';

export default function Sider() {
    const location = useLocation();
    return (
        <Menu mode="inline" selectedKeys={[location.pathname]}>
            <Menu.ItemGroup key="g1" style={{"marginBottom": "20px"}}
                title={ <span className="menu-item-group-title">
                    <LineChartOutlined />&ensp;
                    <span>用户行为时间分析</span>
                </span> }
            >
                <Menu.Item key="/user-day" className="menu-item"><Link to="/user-day">以天为单位</Link></Menu.Item>
                <Menu.Item key="/user-hour" className="menu-item"><Link to="/user-hour">以小时为单位</Link></Menu.Item>
                <Menu.Item key="/user-hour-compare" className="menu-item"><Link to="/user-hour-compare">以小时为单位（叠加比较）</Link></Menu.Item>
            </Menu.ItemGroup>
            <Menu.ItemGroup key="g2" style={{"marginBottom": "20px",}}
                title={ <span className="menu-item-group-title">
                    <PayCircleOutlined />&ensp;
                    <span>用户行为路径分析</span>
                </span> }
            >
                <Menu.Item key="/bhv-cnt" className="menu-item"><Link to="/bhv-cnt">用户购买行为转化</Link></Menu.Item>
                <Menu.Item key="/usr-cnt" className="menu-item"><Link to="/usr-cnt">独立访客购买行为转化</Link></Menu.Item>
            </Menu.ItemGroup>
            <Menu.ItemGroup key="g3" style={{"marginBottom": "20px",}}
                title={ <span className="menu-item-group-title">
                    <PieChartOutlined />&ensp;
                    <span>用户价值分析</span>
                </span> }
            >
                <Menu.Item key="/usr-value" className="menu-item"><Link to="/usr-value">用户价值划分</Link></Menu.Item>
            </Menu.ItemGroup>
        </Menu>
    )
}