function loadChart(domId, title, data) {
    let chart = echarts.init(document.getElementById(domId));
    let option = {
        title: {
            text: `每日${title}趋势图`,
        },
        xAxis: {
            name: "日期",
            type: "time",
        },
        yAxis: {
            name: `${title}`,
            type: "value",
        },
        series: [
            {
                name: title,
                type: "line",
            }
        ],
        grid: {
            containLabel: true,
        },
        tooltip: {
            show: true,
            trigger: "axis",
            formatter: function(params, ticket, callback) {
                return `${params[0].value.day}<br>${title}：${params[0].value.n}`;
            }
        },
        animation: true,
        animationDelay: function (idx) {
            // 越往后的数据时长越大
            return idx * 100;
        },
        dataset: {
            dimensions: ["day", "n"],
            source: data,
        }
    };
    chart.setOption(option);
}