import React from 'react';
import echarts from 'echarts';

import "../styles/BhvCnt.css"

export default class UsrCnt extends React.Component {
    componentDidMount() {
        let option = {
            title: {
                text: "独立访客购买行为转化",
            },
            tooltip: {
                trigger: 'item',
                formatter: "{a} <br/>{b} : {c}%"
            },
            legend: {
                data: ['点击','加入购物车','购买']
            },
        
            series: [
                {
                    name:'独立访客购买行为转化',
                    type:'funnel',
                    minSize: '0%',
                    maxSize: '100%',
                    sort: 'descending',
                    gap: 2,
                    label: {
                        show: true,
                        position: 'inside'
                    },
                    labelLine: {
                        length: 10,
                        lineStyle: {
                            width: 1,
                            type: 'solid'
                        }
                    },
                    itemStyle: {
                        borderColor: '#fff',
                        borderWidth: 1
                    },
                    emphasis: {
                        label: {
                            fontSize: 20
                        }
                    },
                    data: [
                        {value: 100, name: '点击'},
                        {value: 75.09, name: '加入购物车'},
                        {value: 68.33, name: '购买'},
                    ]
                }
            ]
        };
        let chart = echarts.init(document.getElementById("tar"));
        chart.setOption(option);
    }
    render() {
        return (
            <div className="single-container">
                <div className="single-block" id="tar"></div>
            </div>
        )
    }
}